@import "../../global.scss";
.contact{
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    color:white;
    padding-top: 40px;
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
    
    @include mobile{
        flex-direction: column;
        font-size: 10px;
        background-size: 100% 100%;
    }

    .about{
        position: absolute;
        margin-top: 390px;
        text-align: center;
        color:white;

        @include mobile{
          
            font-size: 9px;
            margin-top: 80px;
            
        }

        a:link {
            color: white;
            background-color: transparent;
            
          }
          a:visited {
            color: white;
            background-color: transparent;
            
          }
        
    
          
    }
    
}